:root {
    --primary-color: #295FF4;
    --adm-color-primary: var(--primary-color) !important;
    --font-size-mini: 10px;
    --font-size-small: 16px;
    --font-size-mid: 20px;
    --font-size-big: 30px;
    --border-radius: 20px;
    --backdrop-filter: saturate(180%) blur(20px);
    --success-color: lightgreen;
    --warn-color: yellow;
    --danger-color: red;
    --mid-zindex: 50;
    --max-zindex: 100;
    --box-shadow: 2px 4px 12px var(--main-shadow-color);
    --box-active-shadow: 2px 4px 12px var(--primary-color);
}

:root[theme='day'] {
    --main-bg-color-1: #F2F2F7;
    --main-bg-color-2: #fff;
    --main-text-color-1: #000;
    --main-text-color-2: rgba(0, 0, 0, .5);
    --main-shadow-color: rgba(0, 0, 0, .1);
    --main-glass-color: rgb(245, 245, 247, .5);
}

:root[theme='dark'] {
    --main-bg-color-1: #000;
    --main-bg-color-2: #161617;
    --main-text-color-1: white;
    --main-text-color-2: rgb(255, 255, 255, .5);
    --main-shadow-color: rgb(255, 255, 255, .5);
    --main-glass-color: rgb(0, 0, 0, .1);
}

body {
    background: var(--main-bg-color-1) !important;
    color: var(--main-text-color-1) !important;
    font-size: var(--font-size-small) !important;
}

.adm-modal-body {
    background: var(--main-bg-color-2) !important;
}

.adm-auto-center-content {
    color: var(--main-text-color-1) !important;
}