.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.active {
    color: var(--primary-color);
    font-weight: bold;
}

.sys-empty {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--main-text-color-2);
}


.animate-rotate {
    animation: kf-rotate 1s linear infinite;
}

.animate-breath {
    animation: kf-shadow-breath 2s linear infinite;
}

.animate-popUp {
    animation: animate-popUp .3s linear;
}

.animate-biger-breath {
    animation: animate-biger-breath 2s linear infinite;
}

.animate-shadow-breath {
    animation: kf-shadow-breath 2s linear infinite;
}

@keyframes kf-shadow-breath {
    0% {
        box-shadow: var(--box-shadow);
    }

    50% {
        box-shadow: var(--box-active-shadow);
    }

    100% {
        box-shadow: var(--box-shadow);
    }
}

@keyframes kf-rotate {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

@keyframes animate-popUp {
    from {
        opacity: 0;
        transform: translateY(50vh);
        /* 从底部开始 */
    }

    to {
        opacity: 1;
        transform: translateY(0);
        /* 向上移动到最终位置 */
    }
}

@keyframes animate-popDown {
    from {
        opacity: 0;
        transform: translateY(-50px);
        /* 从底部开始 */
    }

    to {
        opacity: 1;
        transform: translateY(0);
        /* 向上移动到最终位置 */
    }
}

@keyframes animate-biger-breath {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.3);
    }

    100% {
        transform: scale(1);
    }
}

div {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.btn:active {
    transform: scale(0.5);
}

/* 整体滚动条样式 */
::-webkit-scrollbar {
    width: 5px;
    height: 5px;
}

/* 滚动条轨道 */
::-webkit-scrollbar-track {
    background: var(--main-bg-color-1);
}

/* 滚动条滑块 */
::-webkit-scrollbar-thumb {
    background: var(--main-text-color-2);
    border-radius: 4px;
}