.text-input {
    height: calc(100% - 35px);
    width: calc(100% - 40px);
    display: flex;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 35px;
}

.text-send-input {
    width: 100%;
    height: 100%;
    border-radius: 40px;
    border: 5px solid var(--primary-color);
}


.text-send-input input {
    width: calc(100% - 50px - 50px - 20px);
    height: 100%;
    padding: 0 10px;
    border: none;
    outline: none;
    background: var(--main-bg-color-2);
    color: var(--main-text-color-1);
    font-size: var(--font-size-mid);
    border-radius: 0;
}

.text-left-btn {
    width: 40px;
    height: 100%;
    padding-left: 10px;
    border-top-left-radius: 40px;
    border-bottom-left-radius: 40px;
    background: var(--main-bg-color-2);
    color: var(--primary-color);
    font-size: var(--font-size-big);
    font-weight: bold;
    cursor: pointer;
}

.text-right-btn {
    width: 40px;
    height: 100%;
    padding-right: 10px;
    border-top-right-radius: 40px;
    border-bottom-right-radius: 40px;
    background: var(--main-bg-color-2);
    color: var(--main-text-color-2);
    font-size: var(--font-size-big);
    font-weight: bold;
    cursor: pointer;
}

.text-right-btn.ok {
    color: var(--primary-color);
}