.assistant-index {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.assistant-index-head {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: var(--max-zindex);
}

.assistant-index-main {
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0;
    right: 0;
    padding-bottom: 90px;
}

.assistant-index-bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 90px;
    /* background: var(--main-glass-color); */
    /* backdrop-filter: var(--backdrop-filter); */
}

.data-center {
    position: absolute;
    top: 10px;
    right: 30px;
    font-size: var(--font-size-big);
    color: var(--primary-color);
}

.assistant {
    width: calc(100% - 60px);
    height: calc(100% - 60px);
    padding: 30px;
}

.assistant img {
    height: 100%;
}

.assistant-talk {
    position: fixed;
    top: 140px;
    width: 100%;
    z-index: var(--max-zindex);
}

.assistant-talk-content {
    /* background: var(--main-bg-color-2); */
    /* background: var(--main-glass-color); */
    backdrop-filter: var(--backdrop-filter);
    max-width: calc(100% - 60px - 20px);
    max-height: 50vh;
    border: 5px solid var(--primary-color);
    border-radius: var(--border-radius);
    display: flex;
    flex-direction: column;
    padding: 10px;
}

.assistant-talk-ask {
    width: 100%;
    height: 30px;
    font-size: var(--font-size-mid);
    color: var(--primary-color);
    color: white;
    font-weight: bold;
}

.assistant-talk-answer {
    width: 100%;
    height: calc(100% - 30px);
    font-size: var(--font-size-mid);
    color: white;
    color: var(--primary-color);
}

.assistant-talk-content::before {
    content: '';
    position: absolute;
    /* 根据需要调整位置 */
    top: -20px;
    left: 50%;
    transform: translateX(-50%);
    /* 三角形的大小 */
    border-width: 10px;
    border-style: solid;
    /* 三角形的颜色 */
    border-color: transparent transparent var(--primary-color) transparent;
}

.menu-bar {
    width: 100%;
    height: 100%;
    display: flex;
    color: var(--main-text-color-2);
}

.menu-item {
    width: 50%;
    height: 100%;
    cursor: pointer;
    font-size: var(--font-size-mid);
}