/* chat */
.chat-answer {
    position: fixed;
    top: 60px;
    left: 0px;
    right: 0px;
    bottom: 200px;
    padding: 0 10px;
    display: flex;
    flex-direction: column;
    animation: animate-popDown .1s ease-in;
    z-index: var(--max-zindex);
}

.chat-answer-content {
    max-height: calc(100% - 30px);
    background: var(--main-bg-color-2);
    border-radius: var(--border-radius);
    padding: 15px;
    overflow: auto;
    font-size: var(--font-size-mid);
    box-shadow: var(--box-active-shadow);
    word-break: break-all;
}

.chat-ask-text {
    height: 25px;
    width: 100%;
    color: var(--primary-color);
    font-weight: bold;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
    padding-bottom: 10px;
}

.chat-answer-text {
    width: 100%;
    height: calc(100% - 35px);
    overflow: auto;
    transition: 1s all;
}

/* feature */
.feature-list {
    width: calc(100% - 30px);
    padding: 10px 15px 10px 15px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow: auto;
    padding-bottom: 100px;
}

.feature-item-row {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.feature-item {
    flex-shrink: 0;
    background: var(--main-bg-color-2);
    width: 100%;
    border-radius: var(--border-radius);
}

.feature-head {
    width: calc(100% - 20px);
    display: flex;
    padding: 10px;
}

.feature-head-left {
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-weight: bold;
}

.feature-head-right {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: right;
}

.feature-head-right-btn {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    cursor: pointer;
}

.feature-content {
    width: calc(100% - 20px);
    padding: 10px;
}

/* chat-list */
.chat-record {
    position: fixed;
    bottom: 100px;
    left: 25px;
    right: 25px;
}

.chat-record-list {
    width: 100%;
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    gap: 10px;
    height: 60px;
}

.chat-record-list-item {
    flex-shrink: 0;
    height: 50px;
    color: white;
    padding-bottom: 10px;
}

.chat-record-list-content {
    height: 100%;
    padding: 0 10px;
    background: var(--primary-color);
    border-radius: var(--border-radius);
    font-weight: bold;
    cursor: pointer;
}

.chat-record-list-content-del {
    color: red;
    margin-left: 10px;
}

/* habbit */
.habit-list {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    gap: 10px;
}

.habit-item {
    position: relative;
    flex-shrink: 0;
    width: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 10px;
}

.habit-item-ok {
    position: absolute;
    top: 0;
    right: 25%;
    font-size: var(--font-size-big);
    text-align: center;
    line-height: 20px;
    color: var(--success-color);
    font-weight: bold;
    /* animation: animate-popDown .3s linear; */
}

.habit-item-icon {
    font-size: 50px;
}

.habit-item-time {
    font-size: var(--font-size-mini);
    color: var(--primary-color);
    font-weight: bold;
}

.habit-item-title {
    font-size: var(--font-size-small);
    font-weight: bold;
}

.habit-item-stat {
    font-size: var(--font-size-mini);
}

/* env */
.env-hello-text {
    margin-right: 10px;
    font-size: var(--font-size-mid);
    font-weight: bold;
}

.env-temp-text {
    color: var(--main-text-color-2);
}

.env-date {
    display: flex;
    flex-direction: column;
}

.env-time {
    text-align: center;
}

.env-time-info {
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: row;
}

.env-time {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: left;
    font-size: 50px;
}

.env-day {
    width: 50%;
    height: 100%;
    color: var(--primary-color);
    display: flex;
    align-items: center;
    justify-content: right;
    font-size: 30px;
}

.env-location-info {
    height: 30px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: var(--font-size-mid);
    color: var(--main-text-color-2);
}

.env-location {
    display: flex;
    align-items: center;
    width: calc(100% - 30px);
    height: 100%;
}

.env-location-status {
    width: 30px;
    height: 100%;
}

/* event */
.focusthing-item-list {
    display: flex;
    flex-direction: row;
    overflow: auto;
    gap: 10px;
}

.focusthing-item {
    flex-shrink: 0;
    width: 250px;
    background: var(--main-bg-color-1);
    border-radius: var(--border-radius);
    padding: 10px;
    margin-bottom: 10px;
}

.focusthing-item-head {
    display: flex;
}

.focusthing-type {
    width: 50%;
    justify-content: left;
    font-size: var(--font-size-big);
}

.focusthing-time-info {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: right;
    color: var(--main-text-color-2);
}

.focusthing-date {
    display: flex;
    align-items: center;
    justify-content: right;
}

.focusthing-time {
    display: flex;
    align-items: center;
    justify-content: right;
}

.focusthing-item-content {
    display: flex;
    flex-direction: row;
    height: 100px;
}

.focusthing-title {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: var(--font-size-big);
}

.focusthing-left-info {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: var(--primary-color);
    font-size: var(--font-size-mid);
}
.focusthing-left-day {
    font-size: var(--font-size-mid);
    font-weight: bold;
    text-align: center;
}